<template>
  <!-- 商品分类导航 -->
  <div class="type-nav">
    <div class="container">
      <!-- 事件委派: 把子元素的事件交给父元素管理(@mouseleave="leaveIndex") -->
      <div @mouseleave="leaveIndex" @mouseenter="enterShow">
        <h2 class="all">全部商品分类</h2>
        <!-- 三级联动:  -->
        <transition name='sort'>
          <div class="sort" v-show="show">
            <div class="all-sort-list2" @click="goSearch">
              <div class="item" v-for="(c1, index) in categoryList" :key="c1.categoryId"
                :class="{ cur: currentIndex == index }">
                <!-- 一级分类 -->
                <h3 @mouseenter="changeIndex(index)">
                  <a :data-categoryName="c1.categoryName" :data-category1Id="c1.categoryId">{{ c1.categoryName }}</a>
                </h3>
                <!-- 二, 三级分类 -->
                <!-- 鼠标事件:显示或显示三级分类 -->
                <div class="item-list clearfix" :style="{ display: currentIndex == index ? 'block' : 'none' }">
                  <div class="subitem" v-for="(c2, index) in c1.categoryChild" :key="c2.categoryId">
                    <dl class="fore">
                      <dt>
                        <a :data-categoryName="c2.categoryName" :data-category2Id="c2.categoryId">{{ c2.categoryName
                        }}</a>
                      </dt>
                      <dd>
                        <em v-for="(c3, index) in c2.categoryChild" :key="c3.categoryId">
                          <a :data-categoryName="c3.categoryName" :data-category3Id="c3.categoryId">{{ c3.categoryName
                          }}</a>
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav">
        <a href="###">服装城</a>
        <a href="###">美妆馆</a>
        <a href="###">尚品汇超市</a>
        <a href="###">全球购</a>
        <a href="###">闪购</a>
        <a href="###">团购</a>
        <a href="###">有趣</a>
        <a href="###">秒杀</a>
      </nav>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
// 引入lodash
// import _ from 'lodash'
// 按需引入(引入lodash下的throttle)
import throttle from 'lodash/throttle'
export default {
  name: 'TypeNav',
  data() {
    return {
      // 存储用户鼠标移到哪个一级分类
      currentIndex: -1, //定义鼠标移入一级分类初始值
      show: true  //初始值为true(默认一级分类显示)
    }
  },
  // 组件挂载完毕: 可以向服务器发送请求
  mounted() {
    // // 通知Vuex发送请求, 获取数据, 存储在仓库中
    // this.$store.dispatch('categoryList')  //为优化性能这里放到App组件中
    
    // 当组件挂载完毕, 让show的值变为false
    // 如果不是Home组件则将typeNav隐藏
    if (this.$route.path != '/home') {
      this.show = false
    }
  },
  computed: {
    ...mapState({
      categoryList: (state) => {
        return state.home.categoryList;
        console.log(state);
      }
    })
  },
  methods: {
    // 一级分类鼠标移入修改响应式currentIndex属性
    // changeIndex(index) {
    //   this.currentIndex = index;
    // },
    // 设置节流
    changeIndex: throttle(function (index) {
      this.currentIndex = index;
      // console.log('---');
    }, 100),
    // 一级分类鼠标移出修改响应式currentIndex属性
    leaveIndex() {
      this.currentIndex = -1;
      // 鼠标移出, 隐藏search组件的一级分类商品列表显示
      if (this.$route.path != '/home') {
        this.show = false
      }
    },
    // 鼠标移入 显示search组件的一级分类商品列表显示
    enterShow() {
      this.show = true
    },
    // 路由跳转的方法
    goSearch(event) {
      // this.$router.push('/search')
      //获取点击目标
      let element = event.target;
      // 节点中有一个属性dataset, 可以获取到自定义节点的属性与属性值
      // console.log(element.dataset);
      // 自定义属性都为小写
      let { categoryname, category1id, category2id, category3id } = element.dataset;
      // 判断是否为a标签
      if (categoryname) {
        // 整理路由参数
        // 定义location 往 search 跳转
        let location = { name: 'search' };
        // 定义query参数, 初始路径为 categoryName: categoryname 
        let query = { categoryName: categoryname };
        // 判断1, 2, 2级分类
        if (category1id) {
          query.category1Id = category1id;
        } else if (category2id) {
          query.category2Id = category2id;
        } else {
          query.category3Id = category3id;
        }
        // 判断: 如果路由跳转的时候, 带有params参数, 则一起传过去
        if (this.$route.params) {
          //动态给location配置对象添加query参数
          location.query = query;
          //动态给location配置对象添加params参数
          location.params = this.$route.params;
          // 路由跳转
          this.$router.push(location);
        }
      }
    },
  }
}
</script>

<style lang="less" scoped>
.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;
      // overflow-y: auto;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }

          // &:hover {
          //   .item-list {
          //     display: block;
          //   }
          // }
        }

        .cur {
          background-color: skyblue;
        }
      }
    }

    // 一级分类鼠标移入过渡动画
    // 过渡动画开始
    .sort-enter {
      height: 0px;
      opacity: 0.4;
    }

    // 过渡动画结束
    .sort-enter-to {
      height: 461px;
      opacity: 1;
    }

    // 定义动画的过渡时间, 速率
    .sort-enter-active {
      transition: all 0.6s linear;
      overflow: hidden;
    }

    // 一级分类鼠标移出过渡动画
    .sort-leave {
      height: 461px;
      opacity: 1;
    }

    // 过渡动画结束
    .sort-leave-to {
      height: 0px;
      opacity: 0.4;
    }

    // 定义动画的过渡时间, 速率
    .sort-leave-active {
      transition: all 0.4s linear;
      overflow: hidden;
    }
  }
}
</style>