<template>
  <div class="cart">
    <h4>全部商品</h4>
    <!-- 导航头 -->
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul class="cart-list" v-for="(cart, index) in cartInfoList" :key="cart.id">
          <li class="cart-list-con1">
            <input type="checkbox" name="chk_list" :checked="cart.isChecked == 1" @change="updateChecked(cart, $event)">
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl">
            <div class="item-msg">米家（MIJIA） 小米小白智能摄像机增强版 1080p高清360度全景拍摄AI增强</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ cart.skuPrice }}.00</span>
          </li>
          <li class="cart-list-con5">
            <a href="javascript:void(0)" class="mins" @click="handler('minus', -1, cart)">-</a>
            <input autocomplete="off" type="text" minnum="1" class="itxt" :value="cart.skuNum"
              @change="handler('change', $event.target.value * 1, cart)">
            <a href="javascript:void(0)" class="plus" @click="handler('add', 1, cart)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ cart.skuNum * cart.skuPrice }}</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="deleteCartById(cart)">删除</a>
            <br>
            <a href="#none">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll" type="checkbox" :checked="isAllCheck&&cartInfoList.length>0" @change="updateAllCartChecked">
        <span>全选</span>
      </div>
      <div class="option">
        <a @click="deleteAllCheckedCart">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择
          <span>0</span>件商品
        </div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">0</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn" to="/trade">结算</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import throttle from 'lodash/throttle';
export default {
  name: 'ShopCart',
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$store.dispatch('getCartList');
    },
    // 修改购物车中商品的数量, 并添加函数节流
    // async handler: throttle(async function (type, disNum, cart) 
    handler: throttle(async function (type, disNum, cart) {
      // type: 区分这三个元素(+ - 文本框输入)
      // disNum: 变化量: + (1), - (-1), input输入的数量(不是变化量)
      // cart: 区别是哪一个商品(商品身上带有 id)
      // 最终目的: 向服务器发送请求, 服务器响应, 返回数据修改数量
      switch (type) {
        //加号:
        case "add":
          disNum = 1;
          break;
        //减号
        case "minus":
          // 当商品数量大于一时(商品数量才可以减少) 才可以给服务器传递 -1, 如果等于小于1则传递给服务器0 (商品数量不变)
          disNum = cart.skuNum > 1 ? -1 : 0;
          break;
        // 文本框输入
        case "change":
          //如果输入的是非法数字: 带有汉字, 英文, 特殊符号, 负号等 则给服务器传递 0
          if (isNaN(disNum) || disNum < 1) {
            disNum = 0;
          } else {
            // 取整再见与原来的数量 (防止输入的是小数)
            disNum = parseInt(disNum) - cart.skuNum;
          };
          break;
      }
      // 向服务器派发action
      try {
        //修改成功
        await this.$store.dispatch('addOrUpdateShopCart', {
          // 那个商品的id
          skuId: cart.skuId,
          // 商品的数量
          skuNum: disNum,
        });
        // 再次获取服务器最新数据
        this.getData();
      } catch (error) {
        alert(error.message)
      }
    }, 1000),
    // 删除商品操作
    async deleteCartById(cart) {
      try {
        await this.$store.dispatch('deleteCartListBySkuId', cart.skuId);
        this.getData();
      } catch (error) {
        alert(error.message)
      }
    },
    // 修改商品的选中状态
    async updateChecked(cart, event) {
      // 修改商品
      try {
        // 带给服务器的参数isChecked, 不是布尔值, 应该是0|1
        let isChecked = event.target.checked ? '1' : '0';
        await this.$store.dispatch('updateCheckedById', { skuId: cart.skuId, isChecked });
        // 再次发送请求
        this.getData()
      } catch (error) {
        alert(error.message)
      }
    },
    // 删除选中的全部商品
    async deleteAllCheckedCart() {
      try {
        // 派发actions
        await this.$store.dispatch('deleteAllCheckedCart');
        // 重新发送请求
        this.getData();

      } catch (error) {
      }
    },
    // 全部商品的全选
    async updateAllCartChecked(event) {
      try {
        // 判断是否点击, 点击为1 否则为0
      let isChecked = event.target.checked? "1" : "0"
      // 派发action
      await this.$store.dispatch('updateAllCartIsChecked', isChecked);
      this.getData();
      } catch (error) {
        alert(error.message)
      }
    }
  },
  computed: {
    ...mapGetters(['cartList']),
    //数组里的真正数据, 数组
    cartInfoList() {
      return this.cartList.cartInfoList || []
    },
    // 计算购买商品的总价
    totalPrice() {
      let sum = 0
      // 遍历
      this.cartInfoList.forEach(item => {
        sum += item.skuNum * item.skuPrice
      });
      return sum;
    },
    // 判断底部的全选复选框是否勾选
    isAllCheck() {
      // every 遍历cartInfoList数组,  如果数组中有一个为1 则返回false
      return this.cartInfoList.every(item => item.isChecked == 1)
    }
  },
}
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      &>div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;

      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        &>li {
          float: left;
        }

        .cart-list-con1 {
          width: 15%;
        }

        .cart-list-con2 {
          width: 35%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }

        .cart-list-con4 {
          width: 10%;

        }

        .cart-list-con5 {
          width: 17%;

          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 10%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;

          a {
            color: #666;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
</style>